.header {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  height: 84px;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 15;
}

.header.active {
  z-index: 99;
}

.header.active a {
  z-index: 999;
}

.header.active:before {
  content: '';
  width: 100%;
  height: calc(100% + 1px);
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.8);
}

.header h1 {
  font-size: 25.5px;
  font-weight: 600;
  line-height: 0.78;
  text-align: center;
  color: #131c2e;
}

.header a {
  position: absolute;
  top: 30px;
  right: 22px;
}

.header a img {
  height: 35px;
}

body {
  font-family: 'SF UI Display', sans-serif;
  box-sizing: border-box;
  position: relative;
  background-color: #e0e0e0;
}

a {
  text-decoration: none;
}

input[type="submit"] {
  appearance: none;
  -webkit-appearance: none;
}

textarea {
  resize: none;
}

.out.home-page {
  min-height: 100vh;
}

.pages {
  margin-top: 100px;
}

.pages h1 {
  text-align: center;
}

.pages ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.pages a {
  margin-bottom: 15px;
  display: block;
  color: #000000;
}

.block__back {
  height: 84px;
  background-color: #131c2e;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.block__back h1 {
  font-size: 24px;
  font-weight: 600;
  line-height: 0.83;
  text-align: center;
  color: #ffffff;
}

.block__back a {
  position: absolute;
  left: 0px;
  top: 5px;
  padding: 25px;
}

.block__back a i {
  font-size: 24px;
  color: #ffffff;
}

.product-card {
  border-radius: 10px;
  box-shadow: 0 0 3.5px 0 rgba(0, 0, 0, 0.24);
  background-color: #ffffff;
  display: flex;
  padding: 40px 30px;
  margin-bottom: 15px;
}

.product-card__img {
  margin-right: 30px;
  width: 175px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.product-card__img img {
  max-width: 100%;
}

.product-card__info {
  flex: 1 0;
}

.product-card__info__input {
  display: flex;
  position: relative;
}

.product-card__info__input span {
  font-size: 18px;
  color: #131c2e;
  text-transform: uppercase;
  margin-right: 15px;
}

.product-card__info__input input {
  width: 60px;
  border-bottom: 1px solid #bdbdbd;
  font-size: 21px;
  color: #131c2e;
  padding: 0 0 10px;
  padding-right: 15px;
}

.product-card__info__input strong {
  position: absolute;
  top: 0;
  left: 100px;
  transform: rotate(90deg);
  font-size: 26px;
  font-weight: 400;
  color: #9e9e9e;
}

.product-card__info span {
  display: block;
  font-size: 18px;
  color: #9e9e9e;
  margin-bottom: 10px;
}

.product-card__info a {
  font-size: 21px;
  line-height: 1.43;
  color: #131c2e;
}

.product-card__info a.remove {
  font-size: 18px;
  line-height: 1.39;
  color: #9e9e9e;
  margin-top: 20px;
  display: block;
}

.product-card__info b {
  font-size: 24px;
  font-weight: normal;
  color: #f2015b;
  display: block;
  padding: 20px 0;
}

.product-card__info b i {
  font-size: 18px;
  margin-right: 7px;
}

.product-card__info span {
  font-size: 18px;
  color: #131c2e;
  display: block;
}

.product-card__info button {
  border-radius: 24px;
  border: solid 1px #131c2e;
  background-color: #ffffff;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: normal;
  line-height: 1;
  text-transform: uppercase;
  color: #131c2e;
  display: flex;
  align-items: center;
  height: 54px;
  width: 125px;
}

.product-card__info button img {
  display: none;
  height: 22px;
  margin-right: 10px;
}

.product-card__info button.active {
  border: solid 1px #131c2e;
  background-color: #131c2e;
  color: #ffffff;
  width: auto;
}

.product-card__info button.active img {
  display: inline-block;
}

.footer {
  height: 84px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
  background-color: #131c2e;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
  z-index: 15;
}

.footer a {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  color: #ffffff;
  font-weight: normal;
}

.footer__icon {
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer__icon img {
  height: 33px;
  padding-bottom: 2px;
}

.pop-up {
  display: none;
  position: fixed;
  z-index: 99;
}

.pop-up-bg {
  position: fixed;
  display: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  z-index: 98;
}

.pop-up.profile__pop-up {
  width: 100%;
  bottom: 0;
  left: 0;
  background: #ffffff;
}

