.capture-mobile_3__details {
  padding: 24px;
  padding-top: 40px;
}

.capture-mobile_3__form {
  border-radius: 5px;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.13);
  border: solid 0.5px #bfbfbf;
  background-color: #ffffff;
  padding: 30px;
}

.capture-mobile_3__form__select {
  padding-bottom: 40px;
}

.capture-mobile_3__form__select span {
  font-size: 18px;
  color: #9e9e9e;
  display: block;
}

.capture-mobile_3__form__select .selectize-input {
  box-shadow: none;
  border: none;
  border-bottom: 1px solid #bdbdbd;
  padding: 15px 0;
}

.capture-mobile_3__form__select .selectize-input > *, .capture-mobile_3__form__select .selectize-dropdown [data-selectable] {
  font-family: 'SF UI Display', sans-serif;
  font-size: 21px;
  font-weight: 600;
  color: #131c2e;
}

.capture-mobile_3__form__select .selectize-dropdown [data-selectable] {
  padding: 15px 10px;
}

.capture-mobile_3__form__select .selectize-input > input {
  display: none !important;
}

.capture-mobile_3__form__select .selectize-input:after {
  border-width: 8px 10px 0 !important;
}

.capture-mobile_3__form__select .selectize-input.dropdown-active:after {
  border-width: 0px 10px 8px !important;
}

.capture-mobile_3__form__code {
  position: relative;
}

.capture-mobile_3__form__code input {
  width: 100%;
  font-size: 21px;
  font-weight: 600;
  padding: 12px 0;
  color: rgba(19, 28, 46, 0.5);
  border-bottom: 1px solid #bdbdbd;
}

.capture-mobile_3__form__code input::placeholder {
  color: rgba(19, 28, 46, 0.5);
}

.capture-mobile_3__form__code input.invalid {
  border-bottom: 1px solid #fd3e39;
}

.capture-mobile_3__form__code input + label {
  position: absolute;
  left: 0;
  top: 0px;
  font-size: 18px;
  transform: translateY(-25px);
  color: rgba(158, 158, 158, 0.5);
  padding: 12px 0;
  transition: transform 100ms ease;
}

.capture-mobile_3__form__code a {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
}

.capture-mobile_3__form__code a img {
  width: 12px;
}

.capture-mobile_3__form__device {
  padding-top: 30px;
  display: flex;
  align-items: center;
}

.capture-mobile_3__form__device__img {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background-color: #f5f5f5;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 15px;
}

.capture-mobile_3__form__device__img img {
  width: 32px;
}

.capture-mobile_3__form__device__name p {
  font-size: 18px;
  font-weight: normal;
  color: #131c2e;
}

.capture-mobile_3__form__date {
  position: relative;
  margin-top: 30px;
  z-index: 99;
}

.capture-mobile_3__form__date .datepicker-inline {
  position: absolute;
  right: 0;
  z-index: 99;
}

.capture-mobile_3__form__date input {
  width: 100%;
  font-size: 21px;
  font-weight: 600;
  padding: 12px 0;
  color: rgba(19, 28, 46, 0.5);
  border-bottom: 1px solid #bdbdbd;
}

.capture-mobile_3__form__date input::placeholder {
  color: rgba(19, 28, 46, 0.5);
}

.capture-mobile_3__form__date input.invalid {
  border-bottom: 1px solid #fd3e39;
}

.capture-mobile_3__form__date input + label {
  position: absolute;
  left: 0;
  top: 0px;
  display: block;
  width: 100%;
  font-size: 21px;
  color: #9e9e9e;
  padding: 12px 0;
  transition: transform 100ms ease;
}

.capture-mobile_3__form__date input:valid + label {
  transform: translateY(-25px);
  font-size: 18px;
}

.capture-mobile_3__form__date input:valid + label span {
  top: 35px;
  transform: none !important;
}

.capture-mobile_3__form__date input:focus + label {
  transform: translateY(-25px);
  font-size: 18px;
}

.capture-mobile_3__form__date input:focus + label span {
  top: 35px;
  transform: none !important;
}

.capture-mobile_3__form__date span {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
}

.capture-mobile_3__form__date span img {
  width: 24px;
}

.capture-mobile_3__form__download {
  margin-top: 40px;
  height: 150px;
  border-radius: 5px;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.13);
  border: solid 0.5px #bfbfbf;
  background-color: #ffffff;
  position: relative;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.capture-mobile_3__form__download input {
  opacity: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.capture-mobile_3__form__download label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  text-align: center;
  color: #9e9e9e;
  text-transform: uppercase;
}

.capture-mobile_3__form__download label img {
  width: 24px;
  margin-bottom: 15px;
}

.capture-mobile_3__form__file {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 40px;
}

.capture-mobile_3__form input[type="submit"] {
  font-size: 18px;
  font-weight: bold;
  color: #ffffff;
  text-transform: uppercase;
  border-radius: 30px;
  background-color: #131c2e;
  padding: 20px 70px;
  line-height: 1;
  margin-top: 40px;
  position: relative;
  left: 100%;
  transform: translate(-100%);
}

.login {
  padding: 60px 40px;
  background: #f5f5f5;
  height: 100%;
  position: relative;
}

.login__logo {
  width: 103px;
  height: 103px;
  background-color: #131c2e;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  left: 50%;
  transform: translate(-50%);
  margin-bottom: 45px;
}

.login__logo img {
  width: 50px;
}

.login__title {
  margin-bottom: 100px;
}

.login__title h1 {
  font-size: 36px;
  font-weight: 300;
  text-align: center;
  color: #131c2e;
  margin-bottom: 20px;
}

.login__title p {
  font-size: 18px;
  text-align: center;
  color: #131c2e;
}

.login h2 {
  font-size: 28px;
  font-weight: 500;
  text-align: center;
  color: #131c2e;
  text-transform: uppercase;
  position: relative;
  padding-bottom: 15px;
  margin-bottom: 80px;
}

.login h2:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  width: 23px;
  height: 3px;
  border-radius: 1.5px;
  background-color: #ed135d;
}

.login__form {
  padding-bottom: 270px;
}

.login .form__forget a {
  font-size: 18px;
  color: #131c2e;
}

.login .form__phone {
  display: flex;
  padding-bottom: 60px;
}

.login .form__phone__number {
  width: 100%;
  margin-left: 15px;
  position: relative;
}

.login .form__phone__number input {
  font-size: 21px;
  line-height: 2.62;
  color: #9e9e9e;
  width: 100%;
  background: none;
  border-bottom: 1px solid #bdbdbd;
  padding-right: 40px;
}

.login .form__phone__number input::placeholder {
  color: #9e9e9e;
}

.login .form__phone__number a {
  position: absolute;
  right: 0;
  top: 16px;
}

.login .form__phone__number a img {
  width: 30px;
}

.login .form__phone__select {
  display: flex;
  align-items: flex-end;
  width: 95px;
}

.login .form__phone__select .selectize-control {
  width: 95px;
}

.login .form__phone__select .selectize-input {
  border: none;
  box-shadow: none;
  border-bottom: 1px solid #bdbdbd;
  border-radius: 0;
  margin-top: 4px;
  padding: 15px 25px;
  padding-right: 0;
  background-image: url("../img/profile/country-code.png") !important;
  background-repeat: no-repeat !important;
  background-position: 0 12px !important;
}

.login .form__phone__select .selectize-input.full {
  background: none;
}

.login .form__phone__select .selectize-dropdown {
  font-size: 21px;
  text-transform: uppercase;
  color: #9e9e9e;
  font-family: 'SF UI Display', sans-serif;
}

.login .form__phone__select .selectize-dropdown [data-selectable] {
  padding: 10px 20px;
}

.login .form__phone__select .selectize-input > * {
  font-size: 21px;
  color: #9e9e9e;
  text-transform: uppercase;
  font-family: 'SF UI Display', sans-serif;
}

.login .form__phone__select .selectize-input > input {
  display: none !important;
}

.login .form__block {
  margin-bottom: 40px;
  position: relative;
}

.login .form__block input {
  font-size: 21px;
  line-height: 2.62;
  color: #9e9e9e;
  width: 100%;
  background: none;
  border-bottom: 1px solid #bdbdbd;
  padding-right: 40px;
}

.login .form__block input::placeholder {
  color: #9e9e9e;
}

.login .form__block a {
  position: absolute;
  right: 0;
  top: 20px;
}

.login .form__block a i {
  color: #000000;
  font-size: 27px;
}

.login__footer {
  height: 93px;
  background-color: #ffffff;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  border-top: 1px solid #bdbdbd;
  padding: 0 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.login__footer span {
  font-size: 18px;
  color: #131c2e;
}

.login__footer span a {
  font-weight: 600;
  color: #ed135d;
  text-transform: uppercase;
}

.login__footer a.btn {
  border-radius: 30px;
  background-color: #131c2e;
  padding: 20px 70px;
  font-size: 21px;
  font-weight: 600;
  color: #ffffff;
  text-transform: uppercase;
  display: inline-block;
}
